
import Checklist from 'vux/src/components/checklist/index.vue'
import Group from 'vux/src/components/group/index.vue'

import VForm from "./components/form";
export default {
name: "Order",
components: {
Checklist,
Group,
VForm
},
data() {
return {
defaultList: [""],
checkList: ["", ""],
type: 1
};
},
watch: {
defaultList(val, val2) {
var item = val[0];
if (item === "") {
this.type = 1;
} else if (item === "") {
this.type = 2;
} else {
this.defaultList = [val2[0]];
}
}
},
methods: {}
};



